<template>
  <div>
    <div>
      <h1 class="word-v-middle">TSN-CPLEX算法</h1>
    </div>
    <el-form label-width="120px">
      <el-form-item label="信息描述">
        <el-tag type="info">算法Excel模板</el-tag>
        <el-tag>
          <i class="el-icon-download" />
          <a
            :href="'https://onlineedu-jth.oss-cn-beijing.aliyuncs.com/TSN-CPLEX_Template.xlsx'"
            >点击下载模版</a
          >
        </el-tag>
      </el-form-item>

      <el-form-item label="选择Excel">
        <el-upload
          class="upload-demo"
          ref="upload"
          :action="BASE_API + `/algo/algo1/SaveAndCompute`"
          :on-success="fileUploadSuccess"
          :on-error="fileUploadError"
          :on-preview="handlePreview"
          :on-remove="handleRemove"
          :file-list="fileList"
          :disabled="importBtnDisabled"
          :limit="1"
          :auto-upload="false"
        >
        <!-- :action="BASE_API + `/algo/algo1/SaveAndCompute/${this.token}`" -->
          <el-button slot="trigger" size="small" type="primary"
            >选取本地文件</el-button
          >
          <el-button
            style="margin-left: 10px"
            size="small"
            type="success"
            @click="submitUpload"
            >上传并开始求解</el-button
          >
        </el-upload>
      </el-form-item>
    </el-form>

    <el-table :data="tableData" height="400" border style="width: 100%">
      <el-table-column prop="flow" label="数据流" width="180">
      </el-table-column>
      <el-table-column prop="time" label="时间段" width="180">
      </el-table-column>
      <el-table-column prop="road" label="路径"> </el-table-column>
    </el-table>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      BASE_API: axios.defaults.baseURL,
      tableData: [],
      importBtnDisabled: false,
      fileList: [],
      tableData:[],
      token:''
    };
  },
  created() {
    this.token=localStorage.getItem('token');
  },
  methods: {
    //点击按钮上传文件到接口里面
    submitUpload() {
      this.loading = true;
      // js: document.getElementById("upload").submit()
      this.$refs.upload.submit()
    },
    //上传成功
    fileUploadSuccess(response) {
      this.importBtnDisabled = true;
      console.log(response)
      this.tableData=response.data
      console.log('***************'+this.tableData)
      //提示信息
      this.loading = false;
      this.$message({
        type: "success",
        message: "上传成功并开始计算",
      });
    },
    //上传失败
    fileUploadError() {
      this.loading = false;
      this.$message({
        type: "error",
        message: "上传失败",
      });
    },
    handleRemove(file, fileList) {
      console.log(file, fileList);
      

    },
    handlePreview(file) {
      console.log(file);
    },
  },
};
</script>

<style scoped>
.el-container {
  padding: 0;
  margin: 0;
  height: 100%;
}

.header-avatar {
  float: right;
  width: 210px;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.el-dropdown-link {
  cursor: pointer;
}

.el-header {
  background-color: #17b3a3;
  color: #333;
  text-align: center;
  line-height: 60px;
}

.el-aside {
  background-color: #d3dce6;
  color: #333;
  line-height: 200px;
}

.el-main {
  color: #333;
  padding: 0;
}

.a {
  text-decoration: none;
}

.word-v-middle {
  margin-bottom: 0;
  font-size: 20px;
  min-height: 31px;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 31px;
  margin-top: 5px;
  color: #000000;
  white-space: normal;
}
.word-v-middle span {
  text-align: left;
  font-size: 10px;
  text-overflow: -o-ellipsis-lastline;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}
</style>